<template>
    <!-- Page wrapper start -->
    <div class="page-wrapper">

      <!-- Sidebar wrapper start -->
      <Sidebar/>
      <!-- Sidebar wrapper end -->

      <!-- *************
				************ Main container start *************
			************* -->
      <div class="main-container">

        <!-- Page header starts -->
        <Navbar page = "Biblioteca / Distribuição de Recursos"/>
        <!-- Page header ends -->

        <!-- Content wrapper scroll start -->
        <div class="content-wrapper-scroll">

          <!-- Content wrapper start -->
          <div class="content-wrapper">

            <!-- Row start -->
            <div class="row">
              <div class="col-12">
                <div class="card">
                  <div class="card-body">
                      <div class="row mb-2">
                          <div class="col-md-3">
                              <label>Selecione a categoria</label><br>
                              <select
                                class="form-select"
                                v-model = "filter_data.categoria_id"
                                @change="getSubcategorias"
                                >
                                    <option
                                        v-for = "categoria in categorias"
                                        :key = "categoria.id"
                                        :value = "categoria.id"
                                        >
                                    {{categoria.nome}}
                                    </option>
                                </select>
                          </div>
                          <div class="col-md-3">
                              <label>Selecione a subcategoria</label><br>
                              <select
                                class="form-select"
                                v-model = "filter_data.subcategoria_id"
                                @change="getEstante"
                                >
                                    <option
                                        v-for = "subcategoria in subcategorias"
                                        :key = "subcategoria.id"
                                        :value = "subcategoria.id"
                                        >
                                    {{subcategoria.nome}}
                                    </option>
                                </select>
                          </div>
                          <div class="col-md-3">
                              <label>Selecione a estante</label><br>
                              <select
                                class="form-select"
                                v-model = "filter_data.estante_id"
                                @change = "getPrateleira"
                                >
                                    <option
                                        v-for = "estante in estantes"
                                        :key = "estante.id"
                                        :value = "estante.id"
                                        >
                                    {{estante.nome}}
                                    </option>
                                </select>
                          </div>
                           <div class="col-md-3">
                              <label>Selecione a prateleira</label><br>
                              <select
                                class="form-select"
                                v-model = "filter_data.prateleira_id"
                                >
                                    <option
                                        v-for = "prateleira in prateleiras"
                                        :key = "prateleira.id"
                                        :value = "prateleira.id"
                                        >
                                    {{prateleira.nome}}
                                    </option>
                                </select>
                          </div>
                          <div class="col-md-6 d-flex">
                            <div>
                              <label class="text-white">.</label><br>
                              <button @click="getRecursos" class = "btn btn-info btn-sm fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                                <span style="font-size:20px" class = "fas fa-search mx-1"></span> Pesquisar
                              </button>
                            </div>
                            <div>
                              <label class="text-white">.</label><br>
                                <button @click = "createDistribuicao" class = "mx-2 btn btn-primary btn-sm fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                                    <span class = "fas fa-plus mx-1 fs-5"></span> Inserir
                                </button>
                            </div>
                            <div>
                                <label class="text-white">.</label><br>
                                <button data-bs-toggle = "modal" data-bs-target = "#removeForm" class = "btn btn-danger btn-sm fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                                    <span class = "fas fa-trash mx-1 fs-5"></span> Excluir
                                </button>
                            </div>
                          </div>
                      </div>
                      <table id = "myDataTable" class="table table-striped">
                        <thead>
                          <tr class = "bg-secondary">
                            <th>RECURSO</th>
                            <th>AUTOR</th>
                            <th>ISBN</th>
                            <th>EDIÇÃO</th>
                            <th>EDITORA</th>
                            <th>FUNCIONÁRIO</th>
                            <th>DATA DE CRIAÇÃO</th>
                          </tr>
                        </thead>
                        <tbody>
                            <tr v-for = "item in recursos" :key = "item.id" @click="selectRow(item)" :class="{ 'selected': item === selectedRow }">
                               <td>{{item.Recurso.titulo}}</td>
                               <td>{{item.Recurso.autor}}</td>
                               <td>{{item.Recurso.isbn}}</td>
                               <td>{{item.Recurso.edicao}}</td>
                               <td>{{item.Recurso.editora}}</td>
                               <td>{{item.Utilizador.nome}}</td>
                               <td>{{moment(item.createdAt).format("DD-MM-YYYY")}}</td>
                            </tr>
                        </tbody>
                      </table>

                  </div>
                </div>
              </div>
            </div>
            <!-- Row end -->

            <div class="modal fade" id = "removeForm">
                <div class="modal-dialog rounded-0">
                    <div class="modal-content rounded-0">
                        <div class="modal-header">
                           <h4>{{title}}</h4>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <p>
                                    Deseja excluir  <b>{{distribuition_resource.nome}}</b> definitivamente da base de dados?
                                </p>
                            </div>
                        </div>
                        <div class="modal-footer border-0 pt-0">
                            <button class="btn btn-secondary rounded-0" data-bs-dismiss = "modal">Fechar</button>
                            <button @click="deleteForm" class="btn btn-primary rounded-0">Confirmar</button>
                        </div>
                    </div>
                </div>
            </div>

          </div>
          <!-- Content wrapper end -->

          <!-- App Footer start -->
          <Footer/>
          <!-- App footer end -->

        </div>
        <!-- Content wrapper scroll end -->

      </div>
    </div>
    <!-- Page wrapper end -->
</template>

<script>
    import '../static';
    import Sidebar from '../components/Sidebar.vue';
    import Navbar from '../components/Navbar.vue';
    import Footer from '../components/Footer.vue';
    import moment from 'moment'
    import {api} from '../../../helpers/api';
    export default{
        name:"distribuicao_recurso_view",
        components:{Sidebar,Navbar,Footer},
        data(){
          return{
              title:"Distribuicao Recurso",
              dataTable:null,
              selectedRow: null,
              subcategorias:[],
              estantes:[],
              prateleiras:[],
              categorias:[],
              recursos:[],
              distribuition_resource:{
                  id:null,
                  nome:'',
                  capacidade:'',
                  status:true
              },
              filter_data:{
                  categoria_id:null,
                  subcategoria_id:null, 
                  estante_id:null,
                  prateleira_id:null
              }
          }
        },
        mounted() {
          this.getCategories();
        },
        methods:{
            getRecursos(){
              const {categoria_id, subcategoria_id, estante_id, prateleira_id} = this.filter_data;
              api
              .get(`/distribuicao-recurso/categoria/${categoria_id}/subcategoria/${subcategoria_id}/estante/${estante_id}/prateleira/${prateleira_id}`)
              .then(res => {
                  this.recursos = res.data;
              })
              .catch(err => {
                  console.log(err)
              })
            },
             getCategories(){
              api
              .get(`/categoria-recurso/status/1/`)
              .then(res => {
                  this.categorias = res.data;
              })
              .catch(err => {
                  console.log(err)
              })
            },
            getSubcategorias(){
              const {categoria_id} = this.filter_data;
              api
              .get(`/subcategoria-recurso/categoria/${categoria_id}/`)
              .then(res => {
                  this.subcategorias = res.data;
              })
              .catch(err => {
                  console.log(err)
              })
            },
            getEstante(){
              const {categoria_id, subcategoria_id} = this.filter_data;
              api
              .get(`/estante/categoria/${categoria_id}/subcategoria/${subcategoria_id}`)
              .then(res => {
                  this.estantes = res.data;
              })
              .catch(err => {
                  console.log(err)
              })
            },
            getPrateleira(){
              const {categoria_id, subcategoria_id, estante_id} = this.filter_data;
              api
              .get(`/prateleira/categoria/${categoria_id}/subcategoria/${subcategoria_id}/estante/${estante_id}/`)
              .then(res => {
                  this.prateleiras = res.data;
              })
              .catch(err => {
                  console.log(err)
              })
            },
            createDistribuicao(){

                const {categoria_id, subcategoria_id, estante_id, prateleira_id} = this.filter_data;
                if(categoria_id === "" || subcategoria_id === "" || estante_id === "" || prateleira_id === ""){
                    this.$swal.fire({
                        title:"Alerta",
                        icon:"warning",
                        text:"Preencha Todos os campos"
                    })
                }else{
                    const form = {
                        ...this.filter_data,
                        ...this.distribuition_resource,
                        user_id:this.$store.state.user.id
                    }

                    api
                    .post("/distribuicao-recurso/", form)
                    .then(res => {
                        if(res.data.message === "Sucesso"){
                            this.$swal.fire({
                            title:"Sucesso",
                            text:"Recursos Distribuidos com sucesso",
                            icon:"success"
                            })
                        }else{
                            this.$swal.fire({
                            title:"Erro: Não foi possível criar",
                            icon:"error"
                            })
                        }
                    })
                    .catch(err => {
                        this.$swal.fire({
                            title:"Erro",
                            text:err.response.data.message,
                            icon:"error"
                        })
                    })
                }

            },
            deleteForm(){
                api
                .delete(`/distribuicao-recurso/${this.distribuition_resource.id}`)
                .then(res => {
                    if(res.data.message){
                        this.$swal.fire({
                          title:"Sucesso",
                          text:res.data.message,
                          icon:"success"
                        })
                    }else{
                        this.$swal.fire({
                          title:"Erro: Não foi possível excluir",
                          icon:"error"
                        })
                    }
                })
                .catch(err => {
                    console.log(err)
                })
            },
            selectRow(row) {
               this.selectedRow = row;
               this.distribuition_resource = row;
            },
            clearForm(){
                this.distribuition_resource = {};
            },
            moment
        },
        watch: {
            dataTable: {
                handler() {
                    this.selectedRow = null;
                },
                deep: true
            }
        },
    }

</script>

<style>
.selected {
  background-color: blue; 
}
</style>